import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { injectIntl } from 'react-intl';
import styled from 'styled-components';
import QueueAnim from 'rc-queue-anim';
import { withRouter } from 'react-router-dom';
import { Row, Form, Input, Alert, Button, Select, Icon, Radio, Steps } from 'antd';

import PricingTables from './../PricingTables';

import './index.css';

const { Step } = Steps;
const Option = Select.Option;
const countries = require('utils/countries').default;

const radioStyle = {
  display: 'block',
  height: '30px',
  lineHeight: '30px',
};

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 8 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 },
  },
};

const StepsButtons = styled(Button)`
  margin: 5px;
`;

const options = Object.keys(countries).map(key => {
  return (
    <Option key={key} value={key}>
      {countries[key].name}
    </Option>
  );
});

const PayProcess = props => {
  const {
    form,
    history,
    form: { getFieldDecorator },
    intl: { messages },
    process: { processSelected },
  } = props;

  const [current, setCurrent] = useState(0);
  const [showBilling, setShowBilling] = useState('0');
  const [applyTax, setApplyTax] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [secret, setSecret] = useState(null);
  const [formValues, setFormValues] = useState({
    name: '',
    address: '',
    country: 'ES',
    vattax: '',
  });

  useEffect(() => {
    if (!processSelected) {
      history.push('/');
    }
  }, [history, processSelected, props]);

  const onRadioChange = e => {
    setShowBilling(e.target.value);
  };

  const handleChange = value => {
    let aux = formValues;
    aux.country = value;
    setFormValues(aux);
  };

  const next = () => {
    if (current === 1) {
      form.validateFields((err, values) => {
        if (err) {
          return;
        }
        values.continent = countries[values.country].continent;
        if (countries[values.country].continent === 'EU') {
          setApplyTax(true);
        }
        setCurrent(current + 1);
        setFormValues(values);
      });
    } else {
      let sum = 1;
      if (current === 0 && showBilling === '2') {
        sum = 2;
      }
      setCurrent(current + sum);
    }
  };

  const prev = () => {
    let rest = 1;
    if (current === 2 && showBilling === '2') {
      rest = 2;
    }
    setCurrent(current - rest);
  };

  const steps = [
    {
      title: messages.payment.step1,
      icon: <Icon type="question-circle" />,
      content: (
        <React.Fragment>
          <Radio.Group onChange={onRadioChange} defaultValue={showBilling}>
            <Radio style={radioStyle} value="0">
              {messages.payment.question1}
            </Radio>
            <Radio style={radioStyle} value="1">
              {messages.payment.question2}
            </Radio>
            <Radio style={radioStyle} value="2">
              {messages.payment.question3}
            </Radio>
          </Radio.Group>
        </React.Fragment>
      ),
    },
    {
      title: messages.payment.step2,
      icon: <Icon type="solution" />,
      content: (
        <div className="col-12 p-4">
          <Form {...formItemLayout}>
            <Form.Item label={messages.payment.form.name}>
              {getFieldDecorator('name', {
                initialValue: formValues.name,
                rules: [
                  {
                    required: true,
                    message: messages.payment.form.error.name,
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item label={messages.payment.form.address}>
              {getFieldDecorator('address', {
                initialValue: formValues.address,
                rules: [
                  {
                    required: true,
                    message: messages.payment.form.error.address,
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item label={messages.payment.form.country} hasFeedback>
              {getFieldDecorator('country', {
                initialValue: formValues.country,
                rules: [
                  {
                    required: true,
                    message: messages.payment.form.error.country,
                  },
                ],
              })(
                <Select
                  showSearch
                  onChange={handleChange}
                  optionFilterProp="children"
                  filterOption={(input, option) =>
                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }
                  placeholder={messages.payment.form.error.country}
                >
                  {options}
                </Select>
              )}
            </Form.Item>
            {showBilling === '0' && formValues.country === 'ES' && (
              <Form.Item label={messages.payment.form.vattax}>
                {getFieldDecorator('vattax', {
                  initialValue: formValues.vattax,
                  rules: [
                    {
                      required: true,
                      message: messages.payment.form.error.vattax,
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            )}
          </Form>
        </div>
      ),
    },
    {
      title: messages.payment.step3,
      icon: <Icon type="euro" />,
      content: (
        <Row justify="center" type="flex">
          <PricingTables
            secret={secret}
            formValues={formValues}
            processSelected={processSelected || []}
          />

          {applyTax && (
            <div style={{ marginTop: 20 }}>
              <Alert message={messages.payment.alert} type="info" showIcon />
            </div>
          )}
        </Row>
      ),
    },
  ];

  return (
    <QueueAnim type="bottom" className="ui-animate">
      <div key="1" className="row justify-content-md-center">
        <div className="form-card__body col-md-10 col-lg-10 col-xl-8 col-xxl p-4">
          <article className="article">
            <div className="box box-default">
              <div className="box-body p-4">
                <section className="form-v1-container">
                  <h2>{messages.payment.modal}</h2>
                  <Steps current={current}>
                    {steps.map(item => (
                      <Step key={item.title} title={item.title} icon={item.icon} />
                    ))}
                  </Steps>

                  <div className="steps-content">{steps[current].content}</div>
                  <div className="steps-action">
                    {current > 0 && (
                      <StepsButtons style={{ marginLeft: 8 }} onClick={() => prev()}>
                        {messages.payment.previous}
                      </StepsButtons>
                    )}
                    {current < 2 && (
                      <StepsButtons type="primary" onClick={() => next()}>
                        {messages.payment.next}
                      </StepsButtons>
                    )}
                  </div>
                </section>
              </div>
            </div>
          </article>
        </div>
      </div>
    </QueueAnim>
  );
};

function mapStateToProps(state) {
  return {
    process: state.rootReducer.process,
  };
}

export default connect(mapStateToProps)(Form.create()(injectIntl(withRouter(PayProcess))));
