import React, { useState } from 'react';
import { Icon, Button } from 'antd';
import { config } from './../../config';

const PricingTable1 = ({ item, index, secret, formValues, uuid, loadingPay, pay }) => (
  <article className="pricing-table-v1">
    <header style={{ height: 80 }}>
      {' '}
      <h2>{item.option}</h2>{' '}
    </header>
    <p className="pricing-price"> €{item.price.toFixed(2)}</p>
    <p className="pricing-price-detail">
      Aspernatur omnis nemo <br /> omnis porro cupiditate quaera!
    </p>
    <div className="pricing-plan-details" style={{ textAlign: 'left' }}>
      <ul>
        <li>
          <Icon type="check" /> At erat
        </li>
        <li>
          <Icon type="check" /> Sagittis aliquam
        </li>
        <li>
          <Icon type="check" /> Donec enim
        </li>
        <li>
          <Icon type="check" /> Tellus rutrum
        </li>
      </ul>
    </div>
    <footer>
      <Button
        loading={loadingPay === `quote${index}`}
        onClick={() => {
          pay(`quote${index}`);
        }}
        className="btn-cta w-100"
      >
        Get it now
      </Button>
    </footer>
    <form
      hidden
      id={`quote${index}`}
      action={`${config[config.default].hostApi}/api/payment/pay`}
      method="post"
    >
      <input type="text" name="secret" value={secret} />
      <input type="text" name="billing" value={JSON.stringify(formValues)} />
      <input type="text" name="uuid" value={uuid} />
      <input type="text" name="quote" value={item.optionid} />
      <input type="text" name="hostClient" value={config[config.default].hostClient} />
    </form>
  </article>
);

const PricingTable2 = ({ item, index, secret, formValues, uuid, loadingPay, pay }) => (
  <article className="pricing-table-v1 pricing-table-highlight">
    <header style={{ height: 80 }}>
      {' '}
      <h2>{item.option}</h2>{' '}
    </header>
    <p className="pricing-price"> €{item.price.toFixed(2)} </p>
    <p className="pricing-price-detail">
      Aspernatur omnis nemo <br /> omnis porro cupiditate quaera!
    </p>
    <div className="pricing-plan-details" style={{ textAlign: 'left' }}>
      <ul>
        <li>
          <Icon type="check" /> Laoreet id
        </li>
        <li>
          <Icon type="check" /> Quis blandit
        </li>
        <li>
          <Icon type="check" /> 100GB Leo vel
        </li>
        <li>
          <Icon type="check" /> 10 Ac felis
        </li>
      </ul>
    </div>
    <footer>
      <Button
        loading={loadingPay === `quote${index}`}
        onClick={() => {
          pay(`quote${index}`);
        }}
        className="btn-cta w-100"
      >
        Get it now
      </Button>
    </footer>
    <form
      hidden
      id={`quote${index}`}
      action={`${config[config.default].hostApi}/api/payment/pay`}
      method="post"
    >
      <input type="text" name="secret" value={secret} />
      <input type="text" name="billing" value={JSON.stringify(formValues)} />
      <input type="text" name="uuid" value={uuid} />
      <input type="text" name="quote" value={item.optionid} />
      <input type="text" name="hostClient" value={config[config.default].hostClient} />
    </form>
  </article>
);

const Section = ({ processSelected, secret, formValues }) => {
  const [loadingPay, setLoadingPay] = useState(false);
  const pay = async id => {
    setLoadingPay(id);
    document.getElementById(id).submit();
  };
  if (processSelected && processSelected.quotes) {
    return (
      <article className="article">
        <div className="row">
          {processSelected.quotes.map((item, index) => {
            const rest = index % 2 === 0;
            const data = {
              item,
              index,
              secret,
              formValues,
              uuid: processSelected.uuid,
              loadingPay,
              pay,
            };
            return (
              <div className="col-sm-auto col-md-6 col-lg-4 col-xsm-6">
                {rest ? <PricingTable1 {...data} /> : <PricingTable2 {...data} />}
              </div>
            );
          })}
        </div>
      </article>
    );
  }
  return <h1>No Quotes</h1>;
};

export default Section;
